        require('../assets/Teaser_soon.png'),
        require('../assets/Teaser_thumb.png'),
        require('../assets/Teaser_thumb_temp.png'),
      ];
      assetImages.forEach(function(item) {
        preloadImage(item);
      });
      function preloadImage(path){
        let imgTag = document.createElement('img');
        imgTag.src = path;
      }
    },
    preloadVideos(){
      const assetVideos = [
        require('../assets/background.mp4'),
        require('../assets/Teaser.mp4'),
      ];
      this.preloadLists = assetVideos;
      assetVideos.forEach(function(item) {
        preloadImage(item);
      });
      function preloadImage(path){
        let imgTag = document.createElement('video');
        imgTag.src = path;
      }
    },
    finishLoad(){

    }
